<template>
    <SingleLayout>
        <ThreadPreview v-for="picture in pictures" :key="picture.id" :picture="picture"/>
    </SingleLayout>
</template>

<script>
import SingleLayout from '@/components/templates/SingleLayout'
import ThreadPreview from '@/components/molecules/ThreadPreview'
import PicturesApi from '@/api/pictures'

export default {
  name: 'Index',
  components: {
    SingleLayout,
    ThreadPreview
  },
  data () {
    return {
      pictures: []
    }
  },
  created: function() {
    return PicturesApi.list()
      .then((list) => {
        console.log(list)
        this.pictures = list
      })
      .catch(e => {throw e})
  }
}
</script>

<style scoped>

</style>



